import React, { Component } from 'react';
import ReactPaginate from 'react-paginate';


export default class Users extends Component {
  constructor(props) {
    super(props);
    this.state = {
      users: {data:[]},
      offset: 0,
    };
  }

  fetchUser=(page=0)=> {
    const url = `https://reqres.in/api/users?page=${page+1}`
    fetch(url).then(d=>d.json())
    .then(d=> this.setState({users: d}))
    .catch(e=>console.log(e));
  }

  componentDidMount() {
    this.fetchUser();
  }

  handlePageClick = (data) => {
    this.fetchUser(data.selected)
  };

  render() {
    const {users} = this.state;
    return (
      <div >
        {users.data.map((d,i)=>
        <div key={d.id} className="user">
          <img src={d.avatar} alt={`avatar-${d.id}`}/>
          <div style={{textAlign:'left'}}>
          <p>Name: {`${d.first_name} ${d.last_name}`}</p>
          <p>Email: {d.email}</p>
          </div>
        </div>)}
        <div className="react-paginate">
        <ReactPaginate
          previousLabel={'previous'}
          nextLabel={'next'}
          breakLabel={'...'}
          breakClassName={'break-me'}
          pageCount={users.total_pages}
          marginPagesDisplayed={2}
          pageRangeDisplayed={5}
          onPageChange={this.handlePageClick}
          containerClassName={'pagination'}
          activeClassName={'active'}
        />
        </div>
      </div>
    );
  }
}